import React from 'react';
/**
 * Renders a header element with a background image and content.
 * 
 * @param {Object} props - The props object.
 * @param {string} props.title - The title of the header.
 * @param {string} props.image - The URL of the background image.
 * @param {string|JSX.Element} props.children - The content to be displayed inside the header.
 * @returns {JSX.Element} The rendered header component.
 */
const Header = ({title, image, children}) => {
  return (
    <header className='header'>
      <div className='header__container'>
        <div className='header__container-bg'>
          <img src={image} alt="Header Background" />
        </div>
        <div className='header__content'>
          <h2>{title}</h2>
          <p>{children}</p>
        </div>
      </div>
    </header>
  );
}

export default Header