import React from 'react';
import Card from '../UI/Card'


/**
 * Renders a card with information about a trainer.
 * @param {Object} props - The props object.
 * @param {string} props.image - The URL of the trainer's image.
 * @param {string} props.name - The name of the trainer.
 * @param {string} props.job - The job title of the trainer.
 * @param {Array} props.socials - An array of social media objects, each containing an `icon` and a `link`.
 * @returns {JSX.Element} - The rendered Trainer component.
 */
const Trainer = ({ image, name, job, socials }) => {
  return (
    <Card className='trainer'>
      <div className='trainer__img'>
        <img src={image} alt={name} />
      </div>
      <h3>{name}</h3>
      <p>{job}</p>
      <div className='trainer__socials'>
        {socials.map(({ icon, link }, index) => (
          <a key={index} href={link} target="_blank" rel='noopener noreferrer'>
            {icon}
          </a>
        ))}
      </div>
    </Card>
  );
};

export default Trainer